<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
        <div style="height: 10px"></div>
        <table-component
                ref="table"
                template="list_online_examine"
                @handleOperate="handleOperate"
                :columns="[]"
                :request="tableRequest">
        </table-component>
    </div>
</template>

<script>
    import TopButtons from './components/TopButtons'
    import {getUserExamPaperList, deleteExamPaper} from '@/api/examPaper'
    import tableComponent from '@/components/table'
    import filterComponent from '@/components/filter/Index'

    export default {
        components: {
            TopButtons, tableComponent, filterComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                tableRequest: getUserExamPaperList,
                filterFields: [
                    {label: '关键字', field: 'keyword'},
                ],
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'check':
                        this.$router.push({name: '在线考试', params: {id: row.id}})
                        break;
                    case 'delete':
                        deleteExamPaper(row.id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch();
                            });
                        break;
                }
            },
        },
    }
</script>
